export default {
  setCurrentFocus: function ({commit}, platform) {
    commit('SET_CURRENTFOCUS', platform)
  },
  setActiveIndex: function ({commit}, platform) {
    commit('SET_ACTIVEINDEX', platform)
  },
  setPreparationFocus: function ({commit}, platform) {
    commit('SET_PREPARATIONFOCUS', platform)
  },
  setCurrentClassFocus: function ({commit}, platform) {
    commit('SET_CURRENTCLASSFOCUS', platform)
  },
  setCurrentClassActiveIndex: function ({commit}, platform) {
    commit('SET_CURRENTCLASSACTIVEINDEX', platform)
  },
  setCurrentClassDate: function ({commit}, platform) {
    commit('SET_CURRENTCLASSADTATE', platform)
  },
  setCurrentClassInformation: function ({commit}, platform) {
    commit('SET_CURRENTCLASSINFORMATION', platform)
  },
  setConmenDate: function ({commit}, platform) {
    commit('SET_CONMENTDATE', platform)
  },
  setLoginData: function ({commit}, platform) {
    commit('SET_LOGINDATA', platform)
  },
  setCurrentInformationFocus: function ({commit}, platform) {
    commit('SET_CURRENTINFORMATIONFOCUS', platform)
  },
  setCurrentInformationActiveIndex: function ({commit}, platform) {
    commit('SET_CURRENTINFORMATIONACTIVEINDEX', platform)
  },
  setlessonPreparation: function ({commit}, platform) {
    commit('SET_LESSONPREPARATION', platform)
  },
  setTokenCode: function ({commit}, platform) {
    commit('SET_TOKENCODE', platform)
  },
  resetAll: function ({commit}) {
    commit('RESET_ALL')
  }
}



