<template>
  <div class="chosseClassroom">
    <loading v-if="loading"></loading>
     <div class="chosseClassroomIn">
       <div class="top">
          <div class="campus">
            <img src="../assets/banji.png" alt="">
            <div class="group">
              <p><strong>{{ContentData.campusName}}</strong></p>
              <p>校长：{{ContentData.headmasterName}}</p>
            </div>
          </div>
          <div class="choseclass">
            <div><img src="../assets/choseclass.png" alt=""></div>
          </div>
       </div>
       <div class="content">
         <swiper :options="swiperOption"
                 ref="mySwiper">
           <swiper-slide v-for="(item,index) in room"
                         :key="index">
             <div @left="moveLeft"
                   @right="moveRight"
                   @click="jumpdetile(item)"
                   :data = "'hotelItem'+index"
                   :ref="'hotelItem'+index" style="position: relative" v-items="{ default: index == getCurrentClassActiveIndex }">
               <div class="hotelImg" :data-img ="999">
                 <img class="yun" src="../assets/yuncai.png" alt="">
                 <img :src="require('../assets/chb/'+(parseInt(index/5))+'.jpg')">
                 <div class="yunbottom">
                   <img src="../assets/yuncaixia.png" alt="">
                   <p>{{item.classroomName}}</p>
                 </div>
               </div>
             </div>
           </swiper-slide>
           <div class="swiper-button-prev" slot="button-prev"></div>
           <div class="swiper-button-next" slot="button-next"></div>
         </swiper>
       </div>
       <div class="logo"><img src="../assets/logo.png" alt=""></div>
     </div>
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import loading from '../public/loading'
export default {
  name: 'chosseClassroom',
  data () {
    return {
      count: 0,
      loading: true,
      swiperOption: {
        slidesPerView: 'auto',
        centeredSlides: true,
        spaceBetween: 0,
        initialSlide: 0,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      ContentData: '',
      room: []
    }
  },
  computed: {
    ...mapGetters([
      'getCurrentClassFocus',
      'getCurrentClassActiveIndex'
    ])
  },
  methods: {
    ...mapActions([
      'setCurrentClassDate',
      'setCurrentClassFocus',
      'setCurrentClassActiveIndex',
      'setConmenDate'
    ]),
    choosingClassroom () {
      alert(1)
    },
    moveLeft () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slidePrev()
      this.$service.move(this.$refs['hotelItem' + swiper.activeIndex][0])
      this.setCurrentClassActiveIndex(swiper.activeIndex)
      this.setCurrentClassFocus(this.$service.getPointerPosition())
    },
    moveRight () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slideNext()
      this.$service.move('right')
      this.setCurrentClassActiveIndex(swiper.activeIndex)
      this.setCurrentClassFocus(this.$service.getPointerPosition())
    },
    loadCampusInfo () {
      this.$post('/tvBox/loadCampusInfo', {tvBoxNumber: 'ceshi'}
      ).then((res) => {
        this.loading = false
        if (parseFloat(res.status) === 200) {
          this.ContentData = res.data
          this.room = res.data.classrooms
          this.setConmenDate(res.data)
          this.$nextTick(() => {
            this.$service.move(this.$service.pointer)
          })
        } else {
          this.$message(res.message)
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    jumpdetile (val) {
      this.$router.push({
        path: '/classInformation'
      })
      this.setCurrentClassDate(val)
    }
  },
  components: {
    swiper,
    swiperSlide,
    loading
  },
  mounted () {
    this.loadCampusInfo()
    if (this.getCurrentClassFocus === '') {
      this.$nextTick(() => {
        this.$service.move(this.$refs.hotelItem0)
      })
    } else {
      let el = this.$service.getEleByPath(this.getCurrentClassFocus.xpath)
      this.$service.move(el)
      let swiper = this.$refs.mySwiper.swiper
      swiper.slideTo(this.getCurrentClassActiveIndex, 50, false)
    }
  }
}
</script>

<style lang="scss" scoped>
  .chosseClassroom{
    font-size: 0.14rem;
    width: 100%;
    height: 100%;
    background: url(~@/assets/bg.png);
    background-size: 100% 100%;
    .chosseClassroomIn{
      padding: 2% 4%;
      .top{
        display: flex;
        justify-content: space-between;
        .campus{
          width: 160px;
          position: relative;
          text-align: center;
          img{
            width: 100%;
            height: 100%;
          }
          .group{
            width: 100%;
            position: absolute;
            bottom: 6px;
            strong{
              font-size: 15px;
              color: #fff500;
              text-shadow: 7px 0px 7px #dcdcdc;
            }
            p{
              color: #fff;
            }
          }
        }
        .choseclass{
          width: 160px;
          img{
            width: 100%;
            height: 100%;
          }
        }
      }
      .content{
        padding-top: 4%;
        width: 100%;
        .swiper-container{
          padding: 4% 5%;
        }
        >>>.swiper-wrapper{
          width: 100%;
        }
        .swiper-slide{
          width: 21%;
          padding: 0 3.5%;
          .hotelImg{
            position: relative;
            border: 5px solid #fff;
            border-radius: 10px;
            margin: 0 auto;
            text-align: center;
            .yun{
              position: absolute;
              width: 50%;
              left: 25%;
              top: -20%;
              z-index: 100;
            }
            img{
              border-radius: 4px;
              width: 100%;
            }
            .yunbottom{
              z-index: 999;
              position: absolute;
              left: -4.5%;
              bottom: -30px;
              width: 110%;
              height: 40px;
              line-height: 40px;
              img{
                width: 100%;height: 40px;
                position: absolute;
                left: 0;
                z-index: -2;
              }
              p{
                width: 100%;
                color: #fff;
                font-size: 16px;
                text-align: center;
              }
            }
          }
        }
      }
    }
    .logo{
      width: 160px;
      position: fixed;
      right: 80px;
      bottom: 20px;
      img{
        width: 100%;
        height: 100%;
      }
    }
  }
</style>



