<template>
  <div class="courseContent">
    <div class="courseContentIn">
      <div class="top">
        <div class="campus">
          <img src="../assets/banji.png" alt="">
          <div class="groupOne">
            <p><strong>{{getConmenDate.campusName}}</strong></p>
            <p>校长：{{getConmenDate.headmasterName}}</p>
          </div>
        </div>
        <div class="choseclass">
          <img src="../assets/pkysh.png" alt="">
          <div>
            <p>备课老师：{{getLoginData.userName}}</p>
            <p></p>
          </div>
        </div>
      </div>
      <div class="content">
        <swiper :options="swiperOption"
                ref="mySwiper">
          <swiper-slide v-for="(item,index) in ContentData"
                        :key="index">
            <div  v-items = "item.epgConfig"
                  @left="moveLeft"
                  @right="moveRight"
                  @click="jumpdetile(index+1)"
                  :data = "'hotelItem'+index"
                  :ref="'hotelItem'+index" style="position: relative">
              <div class="hotelImg">
<!--                <img :src="require(`../assets/zhyi.png`)" alt="">{{item.bgImg}}-->
                <img :src="item.bgImg" alt="">
              </div>
            </div>
          </swiper-slide>
        </swiper>
      </div>
    </div>
    <div class="footer">
      <div class="footerIn">
        <div><span>{{data}}</span><span>{{time}}</span></div>
        <div><img src="../assets/logo.png" alt=""></div>
      </div>
    </div>
  </div>
</template>
<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import {mapActions, mapGetters} from 'vuex'
export default {
  name: 'courseContent',
  data () {
    return {
      count: 0,
      data: '',
      time: '',
      swiperOption: {
        slidesPerView: 'auto',
        centeredSlides: true,
        spaceBetween: 40,
        initialSlide: 3,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      ContentData: [
        {'key': '1', 'title': '网红打卡地1', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': require(`../assets/zhyi.png`)},
        {'key': '2', 'title': '网红打卡地2', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': require(`../assets/zhouer.png`)},
        {'key': '3', 'title': '网红打卡地4', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': require(`../assets/zhousan.png`)},
        {
          'key': '4',
          epgConfig: {default: true},
          'title': '网红打卡地1',
          'desc': '称广州新电视塔，昵称小蛮腰。',
          'bgImg': require(`../assets/zhousi.png`)
        },
        {'key': '5', 'title': '网红打卡地2', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': require(`../assets/zhouwu.png`)},
        {'key': '6', 'title': '网红打卡地4', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': require(`../assets/zhouliu.png`)},
        {'key': '6', 'title': '网红打卡地4', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': require(`../assets/zhouri.png`)}]
    }
  },
  computed: {
    ...mapGetters([
      'getConmenDate',
      'getCurrentClassDate',
      'getLoginData'
    ])
  },
  methods: {
    ...mapActions([
      'setCurrentClassFocus',
      'setCurrentClassActiveIndex',
      'setCurrentClassInformation'
    ]),
    moveLeft () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slidePrev()
      console.log(swiper.activeIndex)
      console.log(['hotelItem' + swiper.activeIndex][0])
      this.$service.move(this.$refs['hotelItem' + swiper.activeIndex][0])
    },
    moveRight () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slideNext()
      this.$service.move('right')
    },
    jumpdetile (val) {
      alert(val)
      this.$router.push({
        path: '/lessonPreparationContents',
        query: {
          count: this.count
        }
      })
    },
    getListScheduleWhenPrepare () {
      this.$post('/safeTvBox/listScheduleWhenPrepare', {
        'token': this.getLoginData.token,
        'param': JSON.stringify({classroomId: this.getCurrentClassDate.classroomId})}
      ).then((res) => {
        this.classInformation = res.data
        console.log(res, '阿萨德就好使')
      }).catch((error) => {
        console.log(error)
      })
    },
    getNowFormatDate () {
      var date = new Date()
      var seperator1 = '/'
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var hour = date.getHours()
      var minutes = date.getMinutes()
      var seconds = date.getSeconds()
      var strDate = date.getDate()

      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      if (hour >= 0 && hour <= 9) {
        hour = '0' + hour
      }
      if (minutes >= 0 && minutes <= 9) {
        minutes = '0' + minutes
      }
      if (seconds >= 0 && seconds <= 9) {
        seconds = '0' + seconds
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate + '' + hour + ':' + minutes + ':' + seconds

      return currentdate
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  mounted () {
    this.$nextTick(() => {
      setInterval(() => {
        this.data = this.getNowFormatDate().slice(0, 10)
        this.time = this.getNowFormatDate().slice(10)
      }, 1000)
    })
    this.$service.move(this.$service.pointer)
  }
}
</script>

<style lang="scss" scoped>
  .courseContent{
    width: 100%;
    height: 100%;
    background: url(~@/assets/kjbj.png);
    background-size: 100% 100%;
    font-size: 0.14px;
    .courseContentIn{
      padding: 0% 4%;
      .top{
        display: flex;
        justify-content: space-between;
        .campus{
          padding-top: 60px;
          width: 160px;
          position: relative;
          text-align: center;
          img{
            width: 100%;
          }
          .groupOne{
            width: 100%;
            position: absolute;
            bottom: 6px;
            strong{
              font-size: 15px;
              color: #fff500;
              text-shadow: 7px 0px 7px #dcdcdc;
            }
            p{
              color: #fff;
            }
          }
        }
        .choseclass{
          position: relative;
          width: 200px;
          img{
            width: 100%;
          }
          p:first-child{
            position: absolute;
            bottom: 76px;
            text-align: center;
            font-size: 15px;
            font-weight: bold;
            color: #fff;
            width: 100%;
            z-index: 99;

          }
          p:last-child{
            position: absolute;
            bottom: 26px;
            text-align: center;
            width: 100%;
            color: #5c6898;

          }
        }
      }
      .content{
        padding-top: 90px;
        width: 100%;
        >>>.swiper-container{
          overflow:inherit;
        }
        >>>.swiper-wrapper{
          width: 100%;
        }
        .swiper-slide{
          width: 10%;
          .hotelImg{
            img{
              opacity: 0.6;
              width: 100%;
            }
          }
        }
        .focusEpg{
          .hotelImg{
            img{
              opacity: 1;
              width: 100%;
            }
          }
        }
      }
    }
    .footer{
      width: 100%;
      position: absolute;
      bottom: 20px;
      padding: 0 4%;
      .footerIn{
        position: relative;
        div {
          &:first-child {
            width: 250px;
            height: 32px;
            line-height: 32px;
            bottom: 40px;
            background: url("../assets/shj.png");
            background-size: 100% 100%;
            padding: 0 20px 0 40px;
            color: #fff;
            font-size: 13px;
            font-weight: bold;
            display: flex;
            justify-content: space-between;
          }

          &:last-child {
            width: 160px;
            position: absolute;
            bottom: 0;
            right: 0;
            img {
              width: 100%;
              height: 100%;
            }
          }
        }
      }
    }
  }
</style>



