<template>
    <div class="login">
      <loading v-if="loading"></loading>
      <div class="loginIn"  v-items="{default:true}">
        <div class="ewm"><span  id="login_container"></span></div>
        <div class="logo"><img src="../assets/logo2.png" alt=""></div>
      </div>
    </div>
</template>

<script>
import loading from '../public/loading'
import {mapActions, mapGetters} from 'vuex'
export default {
  name: 'login',
  data () {
    return {
      isBack: false,
      schoolData: '',
      campusId: '',
      loading: true
    }
  },
  methods: {
    ...mapActions([
      'setLessonPreparation',
      'setTokenCode'
    ]),
    getlogin () {
      alert(this.getTokenCode)
      this.$get('/login/scanCodeLogin', {'code': JSON.stringify(this.getTokenCode), 'flag': 'tvsystem'}
      ).then((res) => {
        this.loading = false
        if (parseFloat(res.status) === 200) {
          alert(1)
          alert(res)
        } else {
          this.$message(res.message)
        }
      }).catch((error) => {
        alert(2)
        console.log(error)
      })
    }
  },
  components: {
    loading
  },
  computed: {
    ...mapGetters([
      'getCurrentClassDate',
      'getConmenDate',
      'getLoginData',
      'getLessonPreparation',
      'getTokenCode'
    ])
  },
  mounted () {
    console.log(this.getLessonPreparation === 'LessonPreparation', '爱思考的即可洒')
    this.loading = false
    this.schoolData = this.getConmenDate
    this.campusId = this.schoolData.campusId
    // console.log(this.schoolData.campusId)
    this.$service.move(this.$service.pointer)
    this.$nextTick(() => {
      var url = encodeURIComponent('http://ps.iseekids.com:8086/login/scanCodeLogin')
      var goto = encodeURIComponent('https://oapi.dingtalk.com/connect/oauth2/sns_authorize?appid=dingoa7paonoun5bhwg1wb&response_type=code&scope=snsapi_login&state=STATE&redirect_uri=' + url)

      // eslint-disable-next-line no-undef
      var obj = DDLogin({
        id: 'login_container', // 这里需要你在自己的页面定义一个HTML标签并设置id，例如<div id="login_container"></div>或<span id="login_container"></span>
        goto: goto,
        style: 'border:none;background-color:#FFFFFF;',
        width: '260',
        height: '276'
      })
      console.log(obj)
      console.log(this.getLessonPreparation === 'LessonPreparation', '啊实打实')
      var _this = this
      var hanndleMessage = function (event) {
        var origin = event.origin
        console.log('origin', event.origin)
        if (origin === 'https://login.dingtalk.com') { // 判断是否来自ddLogin扫码事件。
          var loginTmpCode = event.data // 拿到loginTmpCode后就可以在这里构造跳转链接进行跳转了
          // console.log('loginTmpCode', loginTmpCode)
          alert(loginTmpCode)
          _this.setTokenCode(event.data)
          // var url2 = 'https://oapi.dingtalk.com/connect/oauth2/sns_authorize?appid=dingoa7paonoun5bhwg1wb&response_type=code&scope=snsapi_login&state=STATE&redirect_uri=' + url + '&loginTmpCode=' + loginTmpCode
          if (_this.getLessonPreparation === 'LessonPreparation') {
            window.location.href = 'http://192.168.251.113:8080/videoCelection'
            _this.setLessonPreparation('')
          } else {
            window.location.href = 'http://192.168.251.113:8080/preparation'
            _this.setLessonPreparation('')
          }
          // window.location.href = url2
        }
      }

      if (typeof window.addEventListener !== 'undefined') {
        window.addEventListener('message', hanndleMessage, false)
      } else if (typeof window.attachEvent !== 'undefined') {
        window.attachEvent('onmessage', hanndleMessage)
      }
      document.addEventListener('keyup', (e) => {
        if (e.keyCode === 27) {
          this.$router.push({
            path: '/classInformation'
          })
        }
      })
    })
  }
}
</script>

<style lang="scss" scoped>
  .login{
    width: 100%;
    height: 100%;
    background: url(~@/assets/dlbj.png);
    background-size: 100% 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .ewm{
      padding: 200px 20px 10px 20px;
      width: 300px;
      background: url(~@/assets/ewmkk.png);
      background-size: 100% 100%;
      img{
        width: 100%;
      }
    }
    .logo{
      margin: 0 auto;
      width: 260px;
      padding: 20px;
      img{
        width: 100%;
      }
    }
    .focusEpg {
      transform: scale(1);
    }
  }
</style>



