<template>
<div class="classInformation">
  <loading v-if="loading"></loading>
  <div class="classInformationIn">
    <div class="top">
      <div class="campus">
        <img src="../assets/banji.png" alt="">
        <div class="group">
          <p><strong>{{getConmenDate.campusName}}</strong></p>
          <p>校长：{{getConmenDate.headmasterName}}</p>
        </div>
      </div>
      <div class="choseclass">
        <div @click="LessonPreparation('LessonPreparation')"  v-items ref="LessonPreparation" @left="itemLeft">
          <img src="../assets/bkan.png" alt="">
        </div>
        <div @click="rest" v-items>
          <img src="../assets/chxjsh.png" alt="" ref="rest">
        </div>
      </div>
    </div>
    <div class="content">
      <swiper :options="swiperOption"
              ref="mySwiper">
        <swiper-slide v-for="(item,index) in classInformation.list"
                      :key="index">
          <div  v-items = "{ default: index == getCurrentInformationActiveIndex }"
                @left="moveLeft"
                @right="moveRight"
                @click="jumpdetile(item.scheduleId)"
                @up="moveUp"
                :data = "'hotelItem'+index"
                :ref="'hotelItem'+index" style="position: relative">
            <div class="teacher"><span>主讲老师：{{item.lecturer}}</span></div>
            <div class="hotelImg">
              <img class="" :src="require('../assets/pk/'+(parseInt((index+1)/49))+'.jpg')" alt="">
              <div class="yunbottom">
                <img src="../assets/yuncaixia.png" alt="">
                <div class="title">
                  <p style="padding-top: 1px;">{{item.className}}</p>
                  <p style="font-size: 12px">{{item.classTime}}</p>
                </div>
              </div>
            </div>
          </div>
        </swiper-slide>
        <div class="swiper-button-prev" slot="button-prev"></div>
        <div class="swiper-button-next" slot="button-next"></div>
      </swiper>
    </div>
    <div class="footer">
      <p><span>{{data}}</span><span>{{time}}</span></p>
    </div>
  </div>
</div>
</template>

<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import {mapActions, mapGetters} from 'vuex'
import loading from '../public/loading'
export default {
  name: 'classInformation',
  data () {
    return {
      count: 0,
      data: '',
      time: '',
      class: [],
      loading: true,
      swiperOption: {
        slidesPerView: 'auto',
        centeredSlides: true,
        spaceBetween: 0,
        initialSlide: 0,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      classInformation: ''
    }
  },
  computed: {
    ...mapGetters([
      'getConmenDate',
      'getCurrentClassDate',
      'getLoginData',
      'getCurrentInformationActiveIndex',
      'getCurrentInformationFocus'
    ])
  },
  methods: {
    ...mapActions([
      'setCurrentClassFocus',
      'setCurrentClassActiveIndex',
      'setCurrentInformationFocus',
      'setCurrentInformationActiveIndex',
      'setCurrentClassInformation',
      'setLoginData',
      'setlessonPreparation',
      'resetAll'
    ]),
    itemLeft () {
      this.$service.move(this.$refs.LessonPreparation)
    },
    moveUp () {
      this.$service.move(this.$refs.LessonPreparation)
    },
    moveLeft () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slidePrev()
      console.log(swiper.activeIndex)
      console.log(['hotelItem' + swiper.activeIndex][0])
      this.$service.move(this.$refs['hotelItem' + swiper.activeIndex][0])
      this.setCurrentInformationActiveIndex(swiper.activeIndex)
      this.setCurrentInformationFocus(this.$service.getPointerPosition())
    },
    moveRight () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slideNext()
      this.$service.move('right')
      console.log(swiper.activeIndex)
      this.setCurrentInformationActiveIndex(swiper.activeIndex)
      this.setCurrentInformationFocus(this.$service.getPointerPosition())
    },
    LessonPreparation (val) {
      console.log(val)
      this.setlessonPreparation(val)
      this.$router.push({path: '/login'})
    },
    jumpdetile (val) {
      this.$router.push({path: '/login'})
      this.setlessonPreparation(val)
      this.setCurrentClassInformation(val)
    },
    rest () {
      this.setCurrentClassActiveIndex(0)
      this.setCurrentClassFocus('')
      this.setLoginData('')
      this.resetAll()
      console.log(this.$store)
      this.$router.replace('/')
    },
    getloadClassroomClassInfo () {
      this.$post('/tvBox/loadClassroomClassInfo', {
        classroomId: this.getCurrentClassDate.classroomId}
      ).then((res) => {
        this.loading = false
        if (parseFloat(res.status) === 200) {
          this.classInformation = res.data
          this.class = res.data.list
          this.$nextTick(() => {
            this.$service.move(this.$service.pointer)
            if (this.getCurrentInformationFocus === '') {
              this.$nextTick(() => {
                this.$service.move(this.$refs.hotelItem0)
              })
            } else {
              let el = this.$service.getEleByPath(this.getCurrentInformationFocus.xpath)
              this.$service.move(el)
              let swipers = this.$refs.mySwiper.swiper
              swipers.slideTo(this.getCurrentInformationActiveIndex, 50, false)
            }
          })
        } else {
          this.$message(res.message)
        }
      }).catch((error) => {
        console.log(error)
      })
    },
    getNowFormatDate () {
      var date = new Date()
      var seperator1 = '/'
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var hour = date.getHours()
      var minutes = date.getMinutes()
      var seconds = date.getSeconds()
      var strDate = date.getDate()

      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      if (hour >= 0 && hour <= 9) {
        hour = '0' + hour
      }
      if (minutes >= 0 && minutes <= 9) {
        minutes = '0' + minutes
      }
      if (seconds >= 0 && seconds <= 9) {
        seconds = '0' + seconds
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate + '' + hour + ':' + minutes + ':' + seconds

      return currentdate
    }
  },
  components: {
    swiper,
    swiperSlide,
    loading
  },
  mounted () {
    this.getloadClassroomClassInfo()
    this.$nextTick(() => {
      document.addEventListener('keyup', (e) => {
        if (e.keyCode === 27) {
          this.$router.push({
            path: '/chosseClassroom'
          })
        }
      })
      // if (this.getCurrentInformationFocus === '') {
      //   alert(1)
      //   this.$nextTick(() => {
      //     this.$service.move(this.$refs.hotelItem0)
      //   })
      // } else {
      //   let el = this.$service.getEleByPath(this.getCurrentInformationFocus.xpath)
      //   this.$service.move(el)
      //   let swiper = this.$refs.mySwiper.swiper
      //   swiper.slideTo(this.getCurrentInformationActiveIndex, 50, false)
      // }
      setInterval(() => {
        this.data = this.getNowFormatDate().slice(0, 10)
        this.time = this.getNowFormatDate().slice(10)
      }, 1000)
    })
  }
}
</script>

<style lang="scss" scoped>
  .classInformation{
    width: 100%;
    height: 100%;
    font-size: 0.14rem;
    background: url(~@/assets/pkbg.png);
    background-size: 100% 100%;
    .classInformationIn{
      padding: 2% 4%;
      .top{
        display: flex;
        justify-content: space-between;
        .campus{
          width: 160px;
          position: relative;
          text-align: center;
          img{
            width: 100%;
          }
          .group{
            width: 100%;
            position: absolute;
            bottom: 6px;
            strong{
              font-size: 15px;
              color: #fff500;
              text-shadow: 7px 0px 7px #dcdcdc;
            }
            p{
              color: #fff;
            }
          }
        }
        .choseclass{
          display: flex;
          justify-content: center;
          div{
            width: 80px;
            margin-left: 30px;
            img{
              width: 100%;
            }
          }
        }
      }
      .content{
        padding-top: 40px;
        width: 100%;
        .swiper-container{
          padding: 4% 5%;
        }
        >>>.swiper-wrapper{
          width: 100%;
          padding-right: 5%;
        }
        .swiper-slide{
          width: 22%;
          padding: 0 3%;
          .teacher{
            text-align: center;
            color: #fff;
            span{
              padding: 6px 10px;
              border-radius: 10px;
              background: rgba(0,0,0,0.6);
            }
          }
          .hotelImg{
            position: relative;
            margin: 16px;
            border-radius: 10px;
            text-align: center;
            img{
              border: 4px solid #fff;
              border-radius: 4px;
              width: 100%;
            }
            .yunbottom{
              z-index: 999;
              position: relative;
              left: -4.5%;
              bottom: 10px;
              width: 110%;
              img{
                border:none;
                width: 100%;
                position: absolute;
                left: 0;
                z-index: -2;
              }
              .title{
                width: 100%;
                position: absolute;
                top: 8px;
                color: #fff;
                p{
                  text-align: center;
                }
              }
            }
          }
        }
      }
    }
    .footer{
      width: 250px;
      height: 32px;
      line-height: 32px;
      position: absolute;
      bottom: 40px;
      background: url("../assets/shj.png");
      background-size: 100% 100%;
      padding: 0 20px 0 40px;
      color: #fff;
      font-size: 13px;
      font-weight: bold;
      p{
        display: flex;
        justify-content: space-between;
      }
    }
  }
</style>



