<template>
  <div class="courseware">
    <div class="top" ref="top">
      <div>
        <h4>北京望京西安市大出血撒AV下次中心</h4>
        <p>教室：巴黎</p>
      </div>
      <div>主讲老师：刘欢</div>
      <div></div>
    </div>
    <div class="content"  ref="content" :style="{height:'+ this.heighth+ '} ">
      <swiper :options="swiperOption"
              ref="mySwiper">
        <swiper-slide v-for="(item,index) in ContentData"
                      :key="index">
          <div  v-items = "item.epgConfig"
                @left="moveLeft"
                @right="moveRight"
                @click="jumpdetile"
                :data = "'hotelItem'+index"
                :ref="'hotelItem'+index">
               <div class="videoParent">
                 <video :src="item.bgImg" :id="'hotelItem'+index"></video>
               </div>
          </div>
        </swiper-slide>
      </swiper>
    </div>
    <div class="fullvideo" v-if="show">
       <div class="Barrage">校区:北京望京校区，登录老师：啊实打实、手机号：1831525254653</div>
      <video controls src="https://www.runoob.com/try/demo_source/movie.mp4"></video>
    </div>
    <div class="footer" ref="footer">
      <div class="footerIn">
        <div>
          <p>2010 年 12 月 2 日</p>
          <p>17:30:35</p>
        </div>
        <div><img src="../assets/logo.png" alt=""></div>
      </div>
    </div>
  </div>
</template>
<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
import {mapActions, mapGetters} from 'vuex'
export default {
  name: 'courseware',
  data () {
    return {
      show: false,
      palyVideo: 'autoplay',
      indexInt: 0,
      heighth: '',
      swiperOption: {
        slidesPerView: 'auto',
        centeredSlides: true,
        spaceBetween: 30,
        initialSlide: 0,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      ContentData: [
        { 'key': '1', epgConfig: { default: true }, 'title': '网红打卡地1', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'https://www.runoob.com/try/demo_source/movie.mp4' },
        { 'key': '2', 'title': '网红打卡地2', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'https://www.runoob.com/try/demo_source/movie.mp4' },
        { 'key': '3', 'title': '网红打卡地4', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'https://www.runoob.com/try/demo_source/movie.mp4' }
      ]
    }
  },
  methods: {
    ...mapActions([
      'setCurrentFocus',
      'setActiveIndex'
    ]),
    LessonPreparation () {
      alert(1)
    },
    moveLeft () {
      console.log(this.$refs)
      let swiper = this.$refs.mySwiper.swiper
      swiper.slidePrev()
      this.$service.move(this.$refs['hotelItem' + swiper.activeIndex][0])
      this.setCurrentFocus(this.$service.getPointerPosition())
      this.setActiveIndex(swiper.activeIndex)
      this.$set(this.swiperOption, 'initialSlide', parseFloat(this.getActiveIndex))
    },
    moveRight () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slideNext()
      this.$service.move('right')
      this.setCurrentFocus(this.$service.getPointerPosition())
      this.setActiveIndex(swiper.activeIndex)
      this.$set(this.swiperOption, 'initialSlide', parseFloat(this.getActiveIndex))
    },
    jumpdetile (val) {
      this.$router.push({
        path: '/videoDetailsFull',
        query: {
        }
      })
    }
  },
  computed: {
    ...mapGetters([
      'getCurrentFocus',
      'getActiveIndex'
    ])
  },
  components: {
    swiper,
    swiperSlide
  },
  mounted: function () {
    console.log(this.getActiveIndex)
    this.heighth = document.documentElement.clientHeight - this.$refs.top.offsetHeight - this.$refs.footer.offsetHeight + 'px'
    console.log(this.getCurrentFocus)
    if (this.getCurrentFocus === '') {
      this.$service.move(this.$service.pointer)
    } else {
      let el = this.$service.getEleByPath(this.getCurrentFocus.xpath)
      this.$service.move(el)
      let swiper = this.$refs.mySwiper.swiper
      swiper.slideTo(this.getActiveIndex, 50, false)
    }
    this.$nextTick(function () {
      document.addEventListener('keyup', function (e) {
        if (e.keyCode === 27) {
        }
      })
    })
  }
}
</script>

<style scoped>
  .courseware{
    width: 100%;
    height: 100%;
    background: #90c5bd;
    color: #ffffff;
  }
  .swiper-slide{
    width: 33%;
  }
  /*.swiper-container-android .swiper-slide, >>> .swiper-wrapper{*/
    /*width: 100%;*/
    /*position: relative;*/
    /*left: -33%;*/
  /*}*/
  .swiper-slide video{
    width: 100%;
    height: 100%;
  }
  .hotelText{
    width: 99.6%;
    position: absolute;
    bottom: 0;
    padding: 5px  10px;
    font-size: 12px;
    background: rgba(0,0,0,0.4);
  }
  .top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 30px;
    padding: 20px;
  }
  .full{
    position: fixed;
    width: 100%;
    height: 100%;
  }
  .content{
    padding: 20px 0;
    overflow-y: auto;
    font-size: 20px;
  }
  .content .videoGroup{
    display: flex;
  }
  .content .videoGroup div{
    width: 100px;
    padding: 10px;
  }
  .footer {
    width: 100%;
    position: fixed;
    bottom: 0;
    font-size: 30px;
  }
  .footerIn{
    display: flex;
    align-items: center;
  }
  .footer .footerIn div:first-child{
    flex: 4;
  }
  .footer .footerIn div:last-child{
    flex: 2;
    margin-right: 20px;
  }
  .footer div:last-child img{
    width: 100%;
    height: 100%;
    border-radius: 4px;
  }
  .Barrage {
    width: 100%;
    position: fixed;
    top: 200px;
    text-align: right;
    z-index: 999999999999;
    color: red;
    display: inline-block;
    white-space: nowrap;
    animation: 4s wordsLoop linear infinite normal;
  }
  @keyframes wordsLoop {
    0% {
      transform: translateX(200px);
      -webkit-transform: translateX(200px);
    }
    100% {
      transform: translateX(-100%);
      -webkit-transform: translateX(-100%);
    }
  }

  @-webkit-keyframes wordsLoop {
    0% {
      transform: translateX(200px);
      -webkit-transform: translateX(200px);
    }
    100% {
      transform: translateX(-100%);
      -webkit-transform: translateX(-100%);
    }
  }
  .fullvideo{
    position: fixed;
    top: 0;
    background: rgba(0,0,0,1);
    width: 100%;
    height: 100%;
    z-index: 9999;
  }
  .fullvideo video{
    width: 100%;
    height: 100%;
  }
</style>



