<template>
  <div class="login">
    <div class="top" ref="top">
      <div>
        <h4>北京望京中心</h4>
        <p>教室：巴黎</p>
      </div>
      <div>备课老师：刘鑫</div>
      <div></div>
    </div>
    <div class="content"  ref="content">
      <div v-for="(item, key) in menus"
           class="card"
           :tabindex="key + 1"
           :id="'card'+key"
           :key="key"
           @click="coursewareJump(item ,key)"  v-items = "item.epgConfig">
        <p>{{item.weekday}}</p>
      </div>
    </div>
    <div class="footer" ref="footer">
      <div class="footerIn">
        <div>
          <p>2010 年 12 月 2 日</p>
          <p>17:30:35</p>
        </div>
        <div><img src="../assets/logo.png" alt=""></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'login',
  data () {
    return {
      count: 0,
      menus: [
        {weekday: '周一', time: '10:20 - 11:20', epgConfig: { default: true }},
        {weekday: '周二', time: '10:20 - 11:20'},
        {weekday: '周三', time: '10:20 - 11:20'},
        {weekday: '周四', time: '10:20 - 11:20'},
        {weekday: '周五', time: '10:20 - 11:20'},
        {weekday: '周六', time: '10:20 - 11:20'},
        {weekday: '周日', time: '10:20 - 11:20'}]
    }
  },
  methods: {
    coursewareJump (item) {
      this.$router.push({ // 核心语句
        path: '/LessonPreparationDetails', // 跳转的路径
        query: { // 路由传参时push和query搭配使用 ，作用时传递参数
        }
      })
    }
  },
  mounted () {
    this.$refs.content.style.height = document.documentElement.clientHeight - this.$refs.top.offsetHeight - this.$refs.footer.offsetHeight + 'px'
    this.$service.move(this.$service.pointer)
  }
}
</script>

<style scoped>
  .login{
    width: 100%;
    height: 100%;
    background: #90c5bd;
    color: #ffffff;
    padding: 20px;
  }
  .login .top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 30px;
  }
  .content{
    padding: 20px 0;
    overflow-y: auto;
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    font-size: 20px;
  }
  .content div{
    box-sizing: border-box;
    width: 12%;
    margin: 1%;
    text-align: center;
    padding: 20px;
  }
  .footer {
    width: 100%;
    position: fixed;
    bottom: 0;
    /*padding:0 20px;*/
    font-size: 30px;
  }
  .footerIn{
    display: flex;
    align-items: center;
  }
  .footer .footerIn div:first-child{
    flex: 4;
  }
  .footer .footerIn div:last-child{
    flex: 2;
    margin-right: 20px;
  }
  .footer .footerIn div:last-child img{
    width: 100%;
    height: 100%;
  }
</style>



