<template>
  <div class="videoDetailsFull"  v-items="{class:'curr'}">
    <!--    <div class="Barrage">校区:北京望京校区，登录老师：啊实打实、手机号：1831525254653wwwww</div>-->
    <div class="FullIn">
      <vue-aliplayer-v2
        @ready="handleReady"
        @ended="endVideo"
        ref="VueAliplayerV2"
        id="player-1194076936807171180"
        :options="options" />
    </div>
  </div>
</template>
<script>
export default {
  name: 'videoDetailsFulls',
  data () {
    return {
      show: true,
      options: { // 配置项
        useFlashPrism: true,
        source: 'https://www.runoob.com/try/demo_source/movie.mp4'
      }
    }
  },
  methods: {
    play () {
      if (this.show === true) {
        this.$refs.VueAliplayerV2.play()
      } else {
        this.$refs.VueAliplayerV2.pause()
      }
    },

    pause () {
      this.$refs.VueAliplayerV2.pause()
    },

    replay () {
      this.$refs.VueAliplayerV2.replay()
    },

    getCurrentTime () {
      this.$refs.VueAliplayerV2.getCurrentTime()
    },
    endVideo () {
      this.$router.back()
    },

    /**
       * 播放器事件回调
       */
    handleReady (e) {
      console.log(e)
    }
  },
  mounted () {
    this.getCurrentTime()
    setTimeout(() => {
      console.log(this.getCurrentTime(), 'asdhas')
    }, 1000)
    this.play()
    // if (this.getStatus === 'ended') {
    //
    // }
    console.log(this.getCurrentTime())
    this.$service.move(this.$service.pointer)
    // eslint-disable-next-line no-undef
  }
}
</script>

<style lang="scss" scoped>
  .videoDetailsFull{
    position: relative;
    width: 100%;
    height: 100%;
    background: #000000;
    /*>>> .prism-player{*/
    /*  max-height: 100%!important;*/
    /*}*/
    .Barrage {
      width: 100%;
      position: fixed;
      font-size: 30px;
      top: 100px;
      text-align: right;
      z-index: 999999999999;
      color: red;
      display: inline-block;
      white-space: nowrap;
      animation: 4s wordsLoop linear infinite normal;
    }
    @keyframes wordsLoop {
      0% {
        transform: translateX(200px);
        -webkit-transform: translateX(200px);
      }
      100% {
        transform: translateX(-100%);
        -webkit-transform: translateX(-100%);
      }
    }

    @-webkit-keyframes wordsLoop {
      0% {
        transform: translateX(200px);
        -webkit-transform: translateX(200px);
      }
      100% {
        transform: translateX(-100%);
        -webkit-transform: translateX(-100%);
      }
    }
    .FullIn{
      width: 100%;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      video{
        max-width: 100%;
        max-height: 100%;
      }
    }
    .curr{
      transform: scale(1)!important;
    }
  }
</style>



