<template>
  <div class="courseContent">
    <div class="courseContentIn">
      <div class="top">
        <div class="campus">
          <img src="../assets/banji.png" alt="">
          <div class="groupOne">
            <p><strong>北京望京校区</strong></p>
            <p>校长：张某某</p>
          </div>
        </div>
        <div class="choseclass">
          <img src="../assets/youzj.png" alt="">
          <div>
            <p>{{videoCelection.className}}</p>
            <p>主讲老师：刘欣</p>
          </div>
        </div>
      </div>
      <div class="content">
        <swiper :options="swiperOption"
                ref="mySwiper">
          <swiper-slide v-for="(item,index) in videoCelection.list"
                        :key="index">
            <div  v-items = "item.epgConfig"
                  @left="moveLeft"
                  @right="moveRight"
                  @click="jumpdetile"
                  :data = "'hotelItem'+index"
                  :ref="'hotelItem'+index" style="position: relative">
              <div class="hotelImg">
                <div class="groupTwo">
                  <p>基础A</p>
                  <p>爱神的箭阿克苏</p>
                  <p>18:30-19:15</p>
                  <span>查看课件</span>
                </div>
              </div>
            </div>
          </swiper-slide>
          <div class="swiper-button-prev" slot="button-prev"></div>
          <div class="swiper-button-next" slot="button-next"></div>
        </swiper>
      </div>
    </div>
    <div class="footer">
      <div class="footerIn">
        <div><span>{{data}}</span><span>{{time}}</span></div>
        <div><img src="../assets/logo.png" alt=""></div>
      </div>
    </div>
  </div>
</template>
<script>
import 'swiper/dist/css/swiper.css'
import {mapGetters} from 'vuex'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  name: 'videoCelection',
  data () {
    return {
      count: 0,
      data: '',
      time: '',
      scheduleId: '',
      swiperOption: {
        slidesPerView: 'auto',
        centeredSlides: true,
        spaceBetween: 0,
        initialSlide: 2,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      videoCelection: ''
    }
  },
  methods: {
    getlogin () {
      alert(this.getTokenCode)
      this.$get('/login/scanCodeLogin', {code: this.getTokenCode, state: 'STATE'}
      ).then((res) => {
        this.loading = false
        if (parseFloat(res.status) === 200) {
          alert(1)
          alert(res)
        } else {
          this.$message(res.message)
        }
      }).catch((error) => {
        alert(2)
        console.log(error)
      })
    },
    moveLeft () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slidePrev()
      console.log(swiper.activeIndex)
      console.log(['hotelItem' + swiper.activeIndex][0])
      this.$service.move(this.$refs['hotelItem' + swiper.activeIndex][0])
    },
    moveRight () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slideNext()
      this.$service.move('right')
    },
    getloadChapterInfo () {
      this.$post('/safeTvBox/loadChapterInfo', {
        'token': JSON.stringify(this.getLoginData.token),
        'param': JSON.stringify({scheduleId: this.scheduleId})
      }).then((res) => {
        this.videoCelection = res.data
        console.log(res, '阿萨德就好使')
      }).catch((error) => {
        console.log(error)
      })
    },
    jumpdetile () {
      this.$router.push({
        path: '/courseContent',
        query: {
          count: this.count
        }
      })
    },
    getNowFormatDate () {
      var date = new Date()
      var seperator1 = '/'
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var hour = date.getHours()
      var minutes = date.getMinutes()
      var seconds = date.getSeconds()
      var strDate = date.getDate()

      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      if (hour >= 0 && hour <= 9) {
        hour = '0' + hour
      }
      if (minutes >= 0 && minutes <= 9) {
        minutes = '0' + minutes
      }
      if (seconds >= 0 && seconds <= 9) {
        seconds = '0' + seconds
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate + '' + hour + ':' + minutes + ':' + seconds

      return currentdate
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  computed: {
    ...mapGetters([
      'getCurrentClassInformation',
      'getLoginData',
      'getTokenCode'
    ])
  },
  mounted () {
    this.getlogin()
    this.scheduleId = this.getCurrentClassInformation
    this.getloadChapterInfo()
    this.$nextTick(() => {
      setInterval(() => {
        this.data = this.getNowFormatDate().slice(0, 10)
        this.time = this.getNowFormatDate().slice(10)
      }, 1000)
    })
    this.$service.move(this.$service.pointer)
  }
}
</script>

<style lang="scss" scoped>
  .courseContent{
    width: 100%;
    height: 100%;
    background: url(~@/assets/kjbj.png);
    background-size: 100% 100%;
    font-size: 0.14px;
    .courseContentIn{
      padding: 0% 4%;
      .top{
        display: flex;
        justify-content: space-between;
        .campus{
          padding-top: 60px;
          width: 160px;
          position: relative;
          text-align: center;
          img{
            width: 100%;
          }
          .groupOne{
            width: 100%;
            position: absolute;
            bottom: 6px;
            strong{
              font-size: 15px;
              color: #fff500;
              text-shadow: 7px 0px 7px #dcdcdc;
            }
            p{
              color: #fff;
            }
          }
        }
        .choseclass{
          position: relative;
          width: 200px;
          img{
            width: 100%;
            height: 100%;
          }
          p:first-child{
            position: absolute;
            bottom: 74px;
            text-align: center;
            font-size: 15px;
            font-weight: bold;
            color: #fff;
            width: 100%;
            z-index: 99;

          }
          p:last-child{
            position: absolute;
            bottom: 26px;
            text-align: center;
            width: 100%;
            color: #5c6898;

          }
        }
      }
      .content{
        padding-top: 40px;
        width: 100%;
        .swiper-container{
          padding: 4% 5%;
        }
        >>>.swiper-wrapper{
          width: 100%;
          padding-right: 5%;
        }
        .swiper-slide{
          width: 22%;
          padding: 0 3.2%;
          .hotelImg{
            position: relative;
            border-radius: 10px;
            margin: 0 auto;
            text-align: center;
            height: 210px;
            background: url("../assets/dsk.png");
            background-size: 100% 100%;
          }
          .groupTwo{
            position: relative;
            top: 80px;
            color: #5c6898;
            p{
              &:first-child{
                font-size: 14px;
              }
            }
            span{
              display: inline-block;
              padding: 4px 10px 10px 10px;
              background: url("../assets/buttom.png");
              background-size: 100% 100%;
            }
          }
        }
      }
    }
    .footer{
      width: 100%;
      position: absolute;
      bottom: 20px;
      padding: 0 4%;
      .footerIn{
        position: relative;
        div {
          &:first-child {
            width: 250px;
            height: 32px;
            line-height: 32px;
            bottom: 40px;
            background: url("../assets/shj.png");
            background-size: 100% 100%;
            padding: 0 20px 0 40px;
            color: #fff;
            font-size: 13px;
            font-weight: bold;
            display: flex;
            justify-content: space-between;
          }

          &:last-child {
            width: 160px;
            position: absolute;
            bottom: 0;
            right: 0;
            img {
              width: 100%;
              height: 100%;
            }
          }
        }
      }
    }
  }
</style>



