      console.log(this, '我是this')
      let swiper = this.$refs.swiperTop.swiper
      console.log(this.$refs, 'sadkasd')
      // console.log(swiper)
      swiper.slidePrev()
      this.$service.move(this.$refs['hotelItem' + swiper.activeIndex][0])
    },
    moveDown () {
      let swiper = this.$refs.swiperTop.swiper
      swiper.slideNext()
      this.$service.move('down')
      this.$service.move(this.$refs['hotelItem' + swiper.activeIndex][0])
    },
