      let swiperaOut = this.$refs.swiperTop.swiper
      this.$refs.swiperTopIn[swiperaOut.activeIndex].swiper.slidePrev()
      // this.setPreparationFocus(this.$service.getPointerPosition())
      this.$service.move(this.$refs['hotelItemIn' + this.$refs.swiperTopIn[swiperaOut.activeIndex].swiper.activeIndex][swiperaOut.activeIndex])
