    height: 100%;
    float: left;
  }
  .content .swiperPartRight{
    width: 80%;
    height: 100%;
    float: left;
    padding: 0 28px;
  }
  .gallery-top {
    width: 100%;
    height: 100%;
  }
  .gallery-topIn{
    width: 100%;
    height: 100%;
    text-align: center;
  }
  .gallery-topIn video{
    width: 300px;
    height: 200px;
    vertical-align: center;
    margin-top: 10%;
    /*border: 10px solid #ffffff;*/
    border-radius: 6px;
  }
  .gallery-top  >>>.swiper-button-prev{
    left: 48%;
    right: auto;
    top:12%;
    transform: rotate(90deg);
  }
  .gallery-top  >>>.swiper-button-next{
    left: 48%;
    right: auto;
    top:80%;
    transform: rotate(90deg);
  }
  .gallery-thumbs {
    box-sizing: border-box;
    font-size: 12px;
  }
  .gallery-thumbs .swiper-slide {
    width: 100%;
    height: 10%;
    justify-content: center;
    align-items: center;
    opacity: 0.4;
  }
  .gallery-thumbs .swiper-slide div{
    padding:0.1rem 0.2rem;
  }
  .gallery-thumbs .swiper-slide-active {
    opacity: 1;
  }
  .footer {
    width: 100%;
    position: fixed;
    bottom: 0;
    font-size: 30px;
  }
  .footerIn{
    display: flex;
    align-items: center;
  }
  .footer .footerIn div:first-child{
    flex: 4;
  }
  .footer .footerIn div:last-child{
    flex: 2;
    margin-right: 20px;
  }
  .footer .footerIn div:last-child img{
    width: 100%;
    height: 100%;
  }
  .focusEpg {
    font-size: 15px;
    transform: scale(1)!important;
  }
  .tip{
    position: fixed;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.6);
    left: 0;
    top: 0;
    z-index: 999999;
  }
  .tip .videoDetailsFull{
    width: 100%;
    height: 100%;
  }
</style>



