<template>
 <div class="courseContent">
    <div class="courseContentIn">
      <div class="top">
        <div class="campus">
          <img src="../assets/banji.png" alt="">
          <div class="groupOne">
            <p><strong>北京望京校区</strong></p>
            <p>校长：张某某</p>
          </div>
        </div>
        <div class="choseclass">
            <img src="../assets/kcnrysh.png" alt="">
            <div>
              <p>基础芭蕾A</p>
              <p>主讲老师：刘欣</p>
            </div>
        </div>
      </div>
      <div class="content">
        <swiper :options="swiperOption"
                ref="mySwiper">
          <swiper-slide v-for="(item,index) in ContentData"
                        :key="index">
            <div  v-items = "item.epgConfig"
                  @left="moveLeft"
                  @right="moveRight"
                  @click="jumpdetile"
                  :data = "'hotelItem'+index"
                  :ref="'hotelItem'+index" style="position: relative">
              <div class="part"><span>主讲老师：王源</span></div>
              <div class="hotelImg">
                <img src="../assets/zuosh.png" alt="">
                <img src="../assets/yoush.png" alt="">
                <div class="groupTwo">
                  <video>
                    <source src="http://sjycdn.miaopai.com/stream/r89SOF-1JPUJqH5gUDJkht0Rbqhul-SLeo8nTQ___32.mp4?ssig=8cd27ab8dd551ae8954b494c73f81eb4&time_stamp=1580703986326" type="video/mp4">
                  </video>
                </div>
              </div>
            </div>
          </swiper-slide>
        </swiper>
      </div>
    </div>
     <div class="footer">
       <div class="footerIn">
         <div><span>{{data}}</span><span>{{time}}</span></div>
         <div><img src="../assets/logo.png" alt=""></div>
       </div>
     </div>
 </div>
</template>
<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  name: 'courseContent',
  data () {
    return {
      count: 0,
      data: '',
      time: '',
      swiperOption: {
        slidesPerView: 'auto',
        centeredSlides: true,
        spaceBetween: 0,
        initialSlide: 1,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      ContentData: [
        { 'key': '1', 'title': '网红打卡地1', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'http://gslb.miaopai.com/stream/r89SOF-1JPUJqH5gUDJkht0Rbqhul-SLeo8nTQ__.mp4?vend=miaopai&ssig=bd051f7c267a0f392c296050ff8af501&time_stamp=1580703883315&mpflag=32&unique_id=1580700283033802' },
        { 'key': '2', epgConfig: { default: true }, 'title': '网红打卡地2', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'http://gslb.miaopai.com/stream/r89SOF-1JPUJqH5gUDJkht0Rbqhul-SLeo8nTQ__.mp4?vend=miaopai&ssig=bd051f7c267a0f392c296050ff8af501&time_stamp=1580703883315&mpflag=32&unique_id=1580700283033802' },
        { 'key': '3', 'title': '网红打卡地4', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'http://gslb.miaopai.com/stream/r89SOF-1JPUJqH5gUDJkht0Rbqhul-SLeo8nTQ__.mp4?vend=miaopai&ssig=bd051f7c267a0f392c296050ff8af501&time_stamp=1580703883315&mpflag=32&unique_id=1580700283033802' }
      ]
    }
  },
  methods: {
    moveLeft () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slidePrev()
      console.log(swiper.activeIndex)
      console.log(['hotelItem' + swiper.activeIndex][0])
      this.$service.move(this.$refs['hotelItem' + swiper.activeIndex][0])
    },
    moveRight () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slideNext()
      this.$service.move('right')
    },
    jumpdetile () {
      this.$router.push({
        path: '/videoDetailsFulls',
        query: {
          count: this.count
        }
      })
    },
    getNowFormatDate () {
      var date = new Date()
      var seperator1 = '/'
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var hour = date.getHours()
      var minutes = date.getMinutes()
      var seconds = date.getSeconds()
      var strDate = date.getDate()

      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      if (hour >= 0 && hour <= 9) {
        hour = '0' + hour
      }
      if (minutes >= 0 && minutes <= 9) {
        minutes = '0' + minutes
      }
      if (seconds >= 0 && seconds <= 9) {
        seconds = '0' + seconds
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate + '' + hour + ':' + minutes + ':' + seconds

      return currentdate
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  mounted () {
    this.$nextTick(() => {
      setInterval(() => {
        this.data = this.getNowFormatDate().slice(0, 10)
        this.time = this.getNowFormatDate().slice(10)
      }, 1000)
    })
    this.$service.move(this.$service.pointer)
  }
}
</script>

<style lang="scss" scoped>
  .courseContent{
    width: 100%;
    height: 100%;
    background: url(~@/assets/kjbj.png);
    background-size: 100% 100%;
    font-size: 0.14px;
    .courseContentIn{
      padding: 0% 4%;
      .top{
        display: flex;
        justify-content: space-between;
        .campus{
          padding-top: 60px;
          width: 160px;
          position: relative;
          text-align: center;
          img{
            width: 100%;
          }
          .groupOne{
            width: 100%;
            position: absolute;
            bottom: 6px;
            strong{
              font-size: 15px;
              color: #fff500;
              text-shadow: 7px 0px 7px #dcdcdc;
            }
            p{
              color: #fff;
            }
          }
        }
        .choseclass{
          position: relative;
          width: 200px;
          img{
            width: 100%;
            height: 100%;
          }
          p:first-child{
            position: absolute;
            bottom: 74px;
            text-align: center;
            font-size: 15px;
            font-weight: bold;
            color: #fff;
            width: 100%;
            z-index: 99;

          }
          p:last-child{
            position: absolute;
            bottom: 26px;
            text-align: center;
            width: 100%;
            color: #5c6898;

          }
        }
      }
      .content{
        padding-top: 40px;
        width: 100%;
        .swiper-container{
          padding: 4% 5%;
        }
        >>>.swiper-wrapper{
          width: 100%;
          padding-right: 5%;
        }
        .swiper-slide{
          width: 32%;
          padding: 0 5%;
          .part{
            width: 100%;
            text-align: center;
            margin-bottom: 10px;
            span{
              padding: 4px 10px;
              background: rgba(0,0,0,0.5);
              color: #fff;
              border-radius: 10px;
            }
          }
          .hotelImg{
            position: relative;
            border-radius: 2px;
            margin: 0 auto;
            text-align: center;
            height: 160px;
            background: url("../assets/shp.png");
            background-size: 100% 100%;
            padding: 4px 4px  26px 4px;
            img{
              width: 20px;
              top: 40%;
              &:first-child{
                position: absolute;
                left: -16px;
              }
              &:nth-child(2){
                position: absolute;
                right: -16px;

              }
            }
          }
          .groupTwo{
            border-radius: 14px;
            height: 100%;
            background: #fff;
            overflow: hidden;
            video{
              width: 100%;
              height: 100%;
            }
          }
        }
      }
    }
    .footer{
      width: 100%;
      position: absolute;
      bottom: 20px;
      padding: 0 4%;
      .footerIn{
        position: relative;
        div {
          &:first-child {
            width: 250px;
            height: 32px;
            line-height: 32px;
            bottom: 40px;
            background: url("../assets/shj.png");
            background-size: 100% 100%;
            padding: 0 20px 0 40px;
            color: #fff;
            font-size: 13px;
            font-weight: bold;
            display: flex;
            justify-content: space-between;
          }

          &:last-child {
            width: 160px;
            position: absolute;
            bottom: 0;
            right: 0;
            img {
              width: 100%;
              height: 100%;
            }
          }
        }
      }
    }
  }
</style>



