<template>
  <div class="videoDetailsFull">
    <div style="width: 100%;height: 100%;" v-for="(item,index) in video"
         :key="index"  v-items = "item.epgConfig" @click="jump">
      <div class="Barrage">校区:北京望京校区，登录老师：啊实打实、手机号：1831525254653</div>
      <video :src = "item.bgImg" id="video" autoplay></video>
    </div>
  </div>
</template>
<script>
export default {
  name: 'videoFull',
  data () {
    return {
      show: false,
      video: [{ 'key': '1', epgConfig: { default: true }, 'bgImg': 'https://www.runoob.com/try/demo_source/movie.mp4' }]
    }
  },
  methods: {
    jump: function () {
      this.$nextTick(() => {
        let myVideo = document.getElementById('video')
        console.log(myVideo)
        if (myVideo.paused) {
          myVideo.play()
        } else {
          myVideo.pause()
        }
      })
    }
  },
  mounted () {
    this.$service.move(this.$service.pointer)
    this.$nextTick(function () {
      let myVideo = document.getElementById('video')
      myVideo.addEventListener('ended', () => {
        this.$router.push({ // 核心语句
          path: '/LessonPreparationDetails'
        })
      })
      document.addEventListener('keyup', (e) => {
        if (e.keyCode === 27) {
          this.$router.push({ // 核心语句
            path: '/LessonPreparationDetails'
          })
        }
      })
    })
  }
}
</script>

<style scoped>
  .videoDetailsFull{
    width: 100%;
    height: 100%;
    background: #000000;
  }
  .videoDetailsFull video{
    width: 99%;
    height: 99%;
  }
  .Barrage {
    width: 100%;
    position: fixed;
    top: 200px;
    text-align: right;
    z-index: 999999999999;
    color: red;
    display: inline-block;
    white-space: nowrap;
    animation: 4s wordsLoop linear infinite normal;
  }
  @keyframes wordsLoop {
    0% {
      transform: translateX(200px);
      -webkit-transform: translateX(200px);
    }
    100% {
      transform: translateX(-100%);
      -webkit-transform: translateX(-100%);
    }
  }

  @-webkit-keyframes wordsLoop {
    0% {
      transform: translateX(200px);
      -webkit-transform: translateX(200px);
    }
    100% {
      transform: translateX(-100%);
      -webkit-transform: translateX(-100%);
    }
  }
</style>



