import Vue from 'vue'
import Router from 'vue-router'
import Login11 from '@/components/login11'
import Courseware from '@/components/courseware'
import videoDetails from '@/components/videoDetails'
import videoDetailsFull from '@/components/videoDetailsFull'
import LessonPreparation from '@/components/LessonPreparation'
import LessonPreparationDetails from '@/components/LessonPreparationDetails'
import videoFull from '@/public/videoFull'
import ChosseClassroom from '@/components/chosseClassroom'
import ceshi from '@/components/ceshi'
import Login from '@/components/login'
import ClassInformation from '@/components/classInformation'
import CourseContent from '@/components/courseContent'
import VideoCelection from '@/components/videoCelection'
import VideoDetailsFulls from '@/components/videoDetailsFulls'
import Preparation from '@/components/preparation'
import lessonPreparationContents from '@/components/lessonPreparationContents'
Vue.use(Router)
export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      redirect: 'chosseClassroom'
    },
    {
      path: '/chosseClassroom',
      name: 'chosseClassroom',
      component: ChosseClassroom,
      meta: {
        title: '选择教室'
      }
    },
    {
      path: '/login',
      name: 'login',
      component: Login,
      meta: {
        title: '登录'
      }
    },
    {
      path: '/classInformation',
      name: 'classInformation',
      component: ClassInformation,
      meta: {
        title: '排课信息'
      }
    },
    {
      path: '/videoCelection',
      name: 'videoCelection',
      component: VideoCelection,
      meta: {
        title: '视频选择'
      }
    },
    {
      path: '/courseContent',
      name: 'courseContent',
      component: CourseContent,
      meta: {
        title: '课程内容'
      }
    },
    {
      path: '/login11',
      name: 'login11',
      component: Login11,
      meta: {
        title: '登录'
      }
    },
    {
      path: '/Courseware',
      name: 'Courseware',
      component: Courseware,
      meta: {
        title: '排课'
      }
    },
    {
      path: '/videoDetails',
      name: 'videoDetails',
      component: videoDetails,
      meta: {
        title: ''
      }
    },
    {
      path: '/videoDetailsFulls',
      name: 'videoDetailsFulls',
      component: VideoDetailsFulls,
      meta: {
        title: '视频'
      }
    },
    {
      path: '/preparation',
      name: 'preparation',
      component: Preparation,
      meta: {
        title: '备课'
      }
    },
    {
      path: '/videoDetailsFull',
      name: 'videoDetailsFull',
      component: videoDetailsFull,
      meta: {
        title: ''
      }
    },
    {
      path: '/lessonPreparationContents',
      name: 'lessonPreparationContents',
      component: lessonPreparationContents,
      meta: {
        title: ''
      }
    },
    {
      path: '/LessonPreparation',
      name: 'LessonPreparation',
      component: LessonPreparation,
      meta: {
        title: ''
      }
    },
    {
      path: '/LessonPreparationDetails',
      name: 'LessonPreparationDetails',
      component: LessonPreparationDetails,
      meta: {
        title: ''
      }
    },
    {
      path: '/ceshi',
      name: 'ceshi',
      component: ceshi,
      meta: {
        title: ''
      }
    },
    {
      path: '/videoFull',
      name: 'videoFull',
      component: videoFull,
      meta: {
        title: ''
      }
    }
  ]
})



