<template>
<div class="courseware">
  <div class="top" ref="top">
    <div>
      <h4>北京望京中心</h4>
      <p>教室：巴黎</p>
    </div>
    <div>主讲老师：刘欢</div>
    <div></div>
  </div>
  <div class="content"  ref="content" :style="{height:'+ this.heighth+ '} ">
    <swiper :options="swiperOption"
            ref="mySwiper">
      <swiper-slide v-for="(item,index) in ContentData"
                    :key="index">
        <div  v-items = "item.epgConfig"
             @left="moveLeft"
             @right="moveRight"
              @click="jumpdetile"
              :data = "'hotelItem'+index"
             :ref="'hotelItem'+index" style="position: relative">
          <div class="hotelImg">
            <img :src="item.bgImg"
                 alt="">
          </div>
          <div class="hotelText">
            <p class="title">{{item.title}}</p>
            <p class="desc">{{item.desc}}</p>
          </div>
        </div>
      </swiper-slide>
    </swiper>
  </div>
  <div class="footer" ref="footer">
    <div class="footerIn">
      <div>
        <p>2010 年 12 月 2 日</p>
        <p>17:30:35</p>
      </div>
      <div><img src="../assets/logo.png" alt=""></div>
    </div>
  </div>
</div>
</template>
<script>
import 'swiper/dist/css/swiper.css'
import {swiper, swiperSlide} from 'vue-awesome-swiper'
export default {
  name: 'courseware',
  data () {
    return {
      heighth: '',
      swiperOption: {
        slidesPerView: 'auto',
        centeredSlides: true,
        spaceBetween: 30,
        initialSlide: 2,
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        }
      },
      ContentData: [
        { 'key': '1', 'title': '网红打卡地1', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570797360448&di=462acff96efb6bf156be4c676e815e69&imgtype=0&src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2F80980a7239def04f2763601f340f8de11f3875e1d427c-zjtsfD_fw658' },
        { 'key': '2', 'title': '网红打卡地2', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570797360448&di=37af7108cd78caee59777d74b516631f&imgtype=0&src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2F057c50ab3dc879fe71f73661d8b7bff8b8034b1f28907-LWqMS5_fw658' },
        { 'key': '3', epgConfig: { default: true }, 'title': '网红打卡地4', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570797360448&di=462acff96efb6bf156be4c676e815e69&imgtype=0&src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2F80980a7239def04f2763601f340f8de11f3875e1d427c-zjtsfD_fw658' },
        { 'key': '4', 'title': '网红打卡地1', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570797360448&di=462acff96efb6bf156be4c676e815e69&imgtype=0&src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2F80980a7239def04f2763601f340f8de11f3875e1d427c-zjtsfD_fw658' },
        { 'key': '5', 'title': '网红打卡地2', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570797360448&di=37af7108cd78caee59777d74b516631f&imgtype=0&src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2F057c50ab3dc879fe71f73661d8b7bff8b8034b1f28907-LWqMS5_fw658' },
        { 'key': '6', 'title': '网红打卡地4', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570797360448&di=462acff96efb6bf156be4c676e815e69&imgtype=0&src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2F80980a7239def04f2763601f340f8de11f3875e1d427c-zjtsfD_fw658' },
        { 'key': '7', 'title': '网红打卡地1', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570797360448&di=462acff96efb6bf156be4c676e815e69&imgtype=0&src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2F80980a7239def04f2763601f340f8de11f3875e1d427c-zjtsfD_fw658' },
        { 'key': '8', 'title': '网红打卡地2', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570797360448&di=37af7108cd78caee59777d74b516631f&imgtype=0&src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2F057c50ab3dc879fe71f73661d8b7bff8b8034b1f28907-LWqMS5_fw658' },
        { 'key': '9', 'title': '网红打卡地4', 'desc': '称广州新电视塔，昵称小蛮腰。', 'bgImg': 'https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1570797360448&di=462acff96efb6bf156be4c676e815e69&imgtype=0&src=http%3A%2F%2Fhbimg.b0.upaiyun.com%2F80980a7239def04f2763601f340f8de11f3875e1d427c-zjtsfD_fw658' }
      ]
    }
  },
  methods: {
    moveLeft () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slidePrev()
      console.log(swiper.activeIndex)
      console.log(['hotelItem' + swiper.activeIndex][0])
      this.$service.move(this.$refs['hotelItem' + swiper.activeIndex][0])
    },
    moveRight () {
      let swiper = this.$refs.mySwiper.swiper
      swiper.slideNext()
      this.$service.move('right')
    },
    jumpdetile () {
      this.$router.push({ // 核心语句
        path: '/videoDetails', // 跳转的路径
        query: { // 路由传参时push和query搭配使用 ，作用时传递参数
          count: this.count
        }
      })
    }
  },
  components: {
    swiper,
    swiperSlide
  },
  mounted () {
    this.heighth = document.documentElement.clientHeight - this.$refs.top.offsetHeight - this.$refs.footer.offsetHeight + 'px'
    console.log(this.$refs.content.style.height)
    this.$service.move(this.$service.pointer)
  }
}
</script>

<style scoped>
  .courseware{
    width: 100%;
    height: 100%;
    background: #90c5bd;
    color: #ffffff;
    padding: 20px;
  }
  .swiper-slide{
    width: 20%;
  }
 /*.swiper-container-android .swiper-slide, >>> .swiper-wrapper{*/
 /*  width: 100%;*/
 /*  position: relative;*/
 /*  left: -40%;*/
 /* }*/
  .swiper-slide img{
    width: 100%;
    height: 100%;
  }
  .hotelText{
    width: 99.6%;
    position: absolute;
    bottom: 0;
    padding: 5px  10px;
    font-size: 12px;
    background: rgba(0,0,0,0.4);
  }
  .top{
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 30px;
  }
  .content{
    padding: 20px 0;
    overflow-y: auto;
    font-size: 20px;
  }
  .content .videoGroup{
    display: flex;
  }
  .content .videoGroup div{
    width: 100px;
    padding: 10px;
  }
  .footer {
    width: 100%;
    position: fixed;
    bottom: 0;
    font-size: 30px;
  }
  .footerIn{
    display: flex;
    align-items: center;
  }
  .footer .footerIn div:first-child{
    flex: 4;
  }
  .footer .footerIn div:last-child{
    flex: 2;
    margin-right: 20px;
  }
  .footer div:last-child img{
    width: 100%;
    height: 100%;
    border-radius: 4px;
  }
  .focusEpg::before{
    content: "<";
    height: 0;
    width: 0;
    position: absolute;
    top:40%;
    left: -30px;
    font-size: 30px;
    color: #fcb903;
  }
  .focusEpg::after{
    content: ">";
    height: 0;
    width: 0;
    position: absolute;
    top: 40%;
    font-size: 30px;
    right: -10px;
    color: #fcb903;
  }
</style>



