export default {
  SET_CURRENTFOCUS: function (state, platform) {
    state.userId = platform
  },
  SET_ACTIVEINDEX: function (state, platform) {
    state.activeIndex = platform
  },
  SET_PREPARATIONFOCUS: function (state, platform) {
    state.preparationFocus = platform
  },
  SET_CURRENTCLASSFOCUS: function (state, platform) {
    state.currentClassFocus = platform
  },
  SET_CURRENTCLASSACTIVEINDEX: function (state, platform) {
    state.currentClassActiveIndex = platform
  },
  SET_CURRENTCLASSADTATE: function (state, platform) {
    state.currentClassDate = platform
  },
  SET_CURRENTCLASSINFORMATION: function (state, platform) {
    state.currentClassInformation = platform
  },
  SET_CONMENTDATE: function (state, platform) {
    state.conmenDate = platform
  },
  SET_LOGINDATA: function (state, platform) {
    state.loginData = platform
  },
  SET_CURRENTINFORMATIONFOCUS: function (state, platform) {
    state.currentInformationFocus = platform
  },
  SET_CURRENTINFORMATIONACTIVEINDEX: function (state, platform) {
    state.currentInformationActiveIndex = platform
  },
  SET_LESSONPREPARATION: function (state, platform) {
    state.lessonPreparation = platform
  },
  SET_TOKENCODE: function (state, platform) {
    state.tokenCode = platform
  },
  RESET_ALL: function (state) {
    state.currentFocus = ''
    state.activeIndex = 0
    state.preparationFocus = ''
    state.currentClassFocus = ''
    state.currentClassActiveIndex = 0
    state.currentClassDate = ''
    state.currentClassInformation = ''
    state.conmenDate = ''
    state.loginData = ''
  }
}



