<template>
 <div class="shijian">
     <div>
      <p> <span>{{data}}</span>
        <span>{{time}}</span></p>
     </div>
     <div><img src="../assets/logo.png" alt=""></div>
 </div>
</template>

<script>
export default {
  name: 'shijian',
  data () {
    return {
      data: '',
      time: ''
    }
  },
  methods: {
    getNowFormatDate () {
      var date = new Date()
      var seperator1 = '/'
      var year = date.getFullYear()
      var month = date.getMonth() + 1
      var hour = date.getHours()
      var minutes = date.getMinutes()
      var seconds = date.getSeconds()
      var strDate = date.getDate()

      if (month >= 1 && month <= 9) {
        month = '0' + month
      }
      if (strDate >= 0 && strDate <= 9) {
        strDate = '0' + strDate
      }
      if (hour >= 0 && hour <= 9) {
        hour = '0' + hour
      }
      if (minutes >= 0 && minutes <= 9) {
        minutes = '0' + minutes
      }
      if (seconds >= 0 && seconds <= 9) {
        seconds = '0' + seconds
      }
      var currentdate = year + seperator1 + month + seperator1 + strDate + '' + hour + ':' + minutes + ':' + seconds

      return currentdate
    }
  },
  mounted () {
    setInterval(() => {
      this.getNowFormatDate()
      this.data = this.getNowFormatDate().slice(0, 10)
      this.time = this.getNowFormatDate().slice(10)
    }, 1000)
  }
}
</script>

<style lang="scss" scoped>
.shijian{
  width: 100%;
  padding: 0 40px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 14px;
   div:first-child{
     p{
       font-size: 14px;
       width: 250px;
       height: 32px;
       line-height: 32px;
       bottom: 40px;
       background: url("../assets/shj.png");
       background-size: 100% 100%;
       padding: 0 20px 0 40px;
       color: #fff;
       font-size: 13px;
       font-weight: bold;
       display: flex;
       justify-content: space-between;
       align-items: center;
     }
   }
   div:last-child{
     width: 160px;
     img{
       width: 100%;
     }
   }
}
</style>



